.card-index {
  width: 3.75rem;
  padding: 0 0 0.34rem;
  box-sizing: border-box;
}
.card-index .card-detail {
  border-bottom: 0.1rem solid #F5F5F5;
  padding: 0 0 0.2rem;
}
.card-index .card-detail .detail {
  display: flex;
  margin-bottom: 0.2rem;
}
.card-index .card-detail .detail .detail-i {
  flex: 1 1;
  text-align: center;
  color: #AFAFAF;
}
.card-index .card-detail .detail .detail-i p {
  color: #000;
}
.card-index .card-detail .detail .detail-i a {
  display: block;
  color: #DBA131;
}
.card-index .card-detail .detail .detail-i:not(:last-child) {
  border-right: 1px solid #EEEEEE;
}
.card-index .card-money {
  width: 3.43rem;
  height: 1.5rem;
  border-radius: 0.12rem;
  background: url(../../static/media/money.4206754f.png) no-repeat left center;
  position: relative;
  margin: 0.1rem auto;
}
.card-index .card-money .tit {
  height: 0.17rem;
  color: #ffffff;
  font-size: 0.12rem;
  left: 0.4rem;
  top: 0.2rem;
}
.card-index .card-money p {
  position: absolute;
  color: #fff;
}
.card-index p.money {
  left: 0.4rem;
  top: 0.55rem;
  line-height: 0.4rem;
  color: #ffffff;
  font-size: 0.14rem;
}
.card-index p.money span {
  font-size: 0.3rem;
}
.card-index p.time {
  line-height: 0.17rem;
  opacity: 0.8;
  color: #ffffff;
  font-size: 0.12rem;
  left: 0.4rem;
  bottom: 0.15rem;
}
.card-index p.type {
  line-height: 0.17rem;
  opacity: 0.8;
  color: #ffffff;
  font-size: 0.12rem;
  right: 0.24rem;
  bottom: 0.15rem;
}
.card-index p.go {
  font-size: 0.15rem;
  right: 0.24rem;
  bottom: 0.7rem;
  opacity: 0.8;
}
.card-index .card-control {
  width: 3.4rem;
  height: 0.89rem;
  border-radius: 0.08rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.06rem 0.16rem 0rem rgba(0, 0, 0, 0.1);
  margin: 0 auto;
}
.card-index .card-control img {
  width: 0.46rem;
  margin: 0.22rem;
}
.card-index .card-control .card-control-r {
  padding: 0.13rem 0;
}
.card-index .card-control .card-control-r div {
  line-height: 0.21rem;
  color: #333333;
  font-size: 0.15rem;
  margin-bottom: 0.08rem;
}
.card-index .card-control .card-control-r div span {
  color: #DBA131;
  padding: 0 0.2rem;
}
.card-index .card-control .card-control-r p {
  line-height: 0.17rem;
  color: #dba131;
  font-size: 0.13rem;
}
.card-info {
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
}
.card-info .card-child {
  width: 3.75rem;
}
.card-info .card-child .card-child-i {
  padding: 0.3rem 0.16rem;
}
