.card-detail-i {
  padding: 0.3rem 0.16rem 0.2rem;
  background: #fff;
}
.card-detail-i .detail {
  display: flex;
  margin-bottom: 0.2rem;
}
.card-detail-i .detail .detail-i {
  flex: 1 1;
  text-align: center;
  color: #AFAFAF;
}
.card-detail-i .detail .detail-i p {
  color: #000;
}
.card-detail-i .detail .detail-i a {
  display: block;
  color: #DBA131;
}
.card-detail-i .detail .detail-i:not(:last-child) {
  border-right: 1px solid #EEEEEE;
}
.card-detail-i .card-time {
  width: 3.43rem;
  height: 1.5rem;
  border-radius: 0.12rem;
  background: url(../../static/media/time.995e2b51.png) no-repeat left center;
  position: relative;
  margin-bottom: 0.1rem;
}
.card-detail-i .card-time .tit {
  height: 0.17rem;
  color: #DBA131;
  font-size: 0.12rem;
  left: 0.4rem;
  top: 0.2rem;
}
.card-detail-i .card-time p {
  position: absolute;
  color: #fff;
}
.card-detail-i .card-time p.time {
  line-height: 0.17rem;
  opacity: 0.8;
  color: #ffffff;
  font-size: 0.12rem;
  left: 0.4rem;
  bottom: 0.15rem;
}
.card-detail-i .card-time p.type {
  line-height: 0.17rem;
  opacity: 0.8;
  color: #ffffff;
  font-size: 0.12rem;
  right: 0.24rem;
  bottom: 0.15rem;
}
.card-detail-i .card-time p.money {
  left: 0.4rem;
  top: 0.55rem;
  line-height: 0.4rem;
  color: #ffffff;
  font-size: 0.14rem;
}
.card-detail-i .card-time p.money span {
  font-size: 0.3rem;
}
.child-card {
  float: left;
  width: 1.6rem;
  height: 1.3rem;
  border-radius: 0.04rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.08rem 0.16rem 0rem rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-right: 0.2rem;
  position: relative;
  margin-bottom: 0.2rem;
}
.child-card img {
  position: absolute;
  left: 0;
  top: 0;
  width: 0.55rem;
}
.child-card:nth-of-type(even) {
  margin-right: 0;
}
.child-card p {
  height: 0.21rem;
  color: #333333;
  font-size: 0.15rem;
  margin-bottom: 0.04rem;
}
.child-card .timer {
  height: 0.4rem;
  padding-top: 0.3rem;
}
.child-card .timer span {
  font-size: 0.34rem;
}
.child-card .time {
  line-height: 0.19rem;
  color: #999999;
  font-size: 0.1rem;
}
.no-effect p {
  color: #999999;
}
.no-effect span {
  color: #999999;
}
.no-card {
  padding: 0.9rem 0 0.3rem;
  text-align: center;
}
.no-card img {
  width: 1rem;
}
.no-card p {
  line-height: 0.22rem;
  color: #333333;
  font-size: 0.16rem;
  text-align: center;
  margin-bottom: 0.2rem;
}
.no-card a {
  width: 1.64rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  color: #ffffff;
  font-size: 0.16rem;
  line-height: 0.4rem;
}
